<template>
  <div class="home">
    <div class="content">
      <div class="block block-name">
        <h1 style="margin-top: 0">popovan</h1>
      </div>
      <div class="block block-input">
        <form v-on:submit.prevent="check">
          <input class="input-car" type="text" v-model="carNumber" placeholder="輸入車牌" />
        </form>
      </div>
      <div class="block block-msg" v-if="msg">{{msg}}</div>
      <div class="block block-btn">
        <a class="btn" @click="check">查詢</a>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import {data} from "../scripts/data.js";

export default {
  name: 'home',
  data: function () {
    return {
      carNumber: "",
      msg: "",
      listdata: data.list,
    }
  },
  watch: {
  },
  methods: {    
    check: function(){
      let vm = this;
      vm.msg = '';
      let carNum = vm.carNumber.replace(/\s/g, '').toUpperCase();
      for(var i=0; i<vm.listdata.length; i++){
        let item = vm.listdata[i];
        console.log(item.number)
        console.log(carNum)
        if( item.number.indexOf(carNum) >= 0 ){
          vm.msg = "發現popovan：" + item.number
          alert(vm.msg)
          break;
        }else{
          vm.msg = "可安心乘搭，唔好亂講野"
        }
      }
    }
  },
  mounted: function(){
  }
}
</script>


<style lang="sass">
@import "../styles/variables.sass"
@import "../styles/home.sass"
</style>
